@charset "UTF-8";
.fau-studium-display.degree-program-box {
  background-color: var(--FAU_Col_FAU-Dunkelblau_12_5, #DFE2E7);
  padding: 4em 16px 24px;
  width: 100%;
  max-width: 870px;
  margin: 0 auto;
  display: flow-root;
}
.fau-studium-display.degree-program-box h1 {
  color: var(--FAU_Col_FAU-Blau, #04316A);
  font-size: var(--H-Heading-5, 1.15rem);
  margin-top: 0;
  padding-top: 0;
}
.fau-studium-display.degree-program-box dl.facts {
  margin: 0 0 16px;
  line-height: 1.8;
}
@media screen and (min-width: 768px) {
  .fau-studium-display.degree-program-box dl.facts {
    column-count: 2;
    column-gap: 16px;
  }
}
.fau-studium-display.degree-program-box dl.facts .dpair {
  break-inside: avoid; /* verhindert Spaltenumbruch innerhalb dieses Blocks */
  page-break-inside: avoid; /* für Print */
  display: flow-root;
}
.fau-studium-display.degree-program-box dl.facts dt {
  margin: 0;
  float: left;
  clear: left;
  font-weight: bold;
}
.fau-studium-display.degree-program-box dl.facts dd {
  float: left;
  margin-left: 1rem;
}
.fau-studium-display.degree-program-box dl.special-features {
  margin: 0;
}
.fau-studium-display.degree-program-box dl.special-features dt {
  margin: 0;
}
.fau-studium-display.degree-program-box dl.special-features dd {
  margin-left: 0;
}